'use client'

import { useState, useEffect, useRef } from 'react'
import { useRouter } from 'next/navigation'

interface Message {
  id: string
  sender: string
  text: string
  timestamp: string
}

interface User {
  id: string
  username: string
  anonymousId: string
  verified: boolean
}

interface Member {
  id: string
  anonymousId: string
  isOnline: boolean
}

export default function ChatPage() {
  const [messages, setMessages] = useState<Message[]>([])
  const [newMessage, setNewMessage] = useState('')
  const [user, setUser] = useState<User | null>(null)
  const [members, setMembers] = useState<Member[]>([])
  const [showMembers, setShowMembers] = useState(false)
  const [isLoading, setIsLoading] = useState(true)
  const [isSending, setIsSending] = useState(false)
  const [error, setError] = useState('')
  const messagesEndRef = useRef<HTMLDivElement>(null)
  const router = useRouter()

  // Auto-scroll to bottom when new messages arrive
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' })
  }

  useEffect(() => {
    scrollToBottom()
  }, [messages])

  // Check if user is logged in
  useEffect(() => {
    const userData = localStorage.getItem('user')
    if (!userData) {
      router.push('/login')
      return
    }

    try {
      const parsedUser = JSON.parse(userData)
      setUser(parsedUser)
      setIsLoading(false)
    } catch (error) {
      router.push('/login')
      return
    }
  }, [router])

  // Fetch messages and members
  useEffect(() => {
    if (!user) return

    const fetchMessages = async () => {
      try {
        const response = await fetch('/api/chat')
        if (response.ok) {
          const data = await response.json()
          setMessages(data.messages)
        }
      } catch (error) {
        console.error('Failed to fetch messages:', error)
      }
    }

    const fetchMembers = async () => {
      try {
        const response = await fetch('/api/users')
        if (response.ok) {
          const data = await response.json()
          const verifiedMembers = data.users
            .filter((u: any) => u.verified && u.anonymousId)
            .map((u: any) => ({
              id: u.id,
              anonymousId: u.anonymousId,
              isOnline: Math.random() > 0.3 // Simulate online status
            }))
          setMembers(verifiedMembers)
        }
      } catch (error) {
        console.error('Failed to fetch members:', error)
        // Fallback to empty array
        setMembers([])
      }
    }

    // Initial fetch
    fetchMessages()
    fetchMembers()

    // Poll for updates
    const interval = setInterval(() => {
      fetchMessages()
      // Update online status periodically
      setMembers(prev => prev.map(member => ({
        ...member,
        isOnline: Math.random() > 0.3
      })))
    }, 5000)

    return () => clearInterval(interval)
  }, [user, router])

  const handleSendMessage = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!newMessage.trim() || !user) return

    setIsSending(true)
    setError('')

    try {
      const response = await fetch('/api/chat', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          text: newMessage.trim(),
          sender: user.anonymousId,
        }),
      })

      if (response.ok) {
        setNewMessage('')
        // Messages will be updated by the polling effect
      } else {
        const data = await response.json()
        setError(data.error || 'Failed to send message')
        
        if (response.status === 401) {
          localStorage.removeItem('user')
          router.push('/login')
        }
      }
    } catch (error) {
      setError('Network error. Please try again.')
    } finally {
      setIsSending(false)
    }
  }

  const handleLogout = () => {
    localStorage.removeItem('user')
    // Clear session cookie by calling logout endpoint
    fetch('/api/auth/logout', { method: 'POST' }).finally(() => {
      router.push('/')
    })
  }

  const formatTime = (timestamp: string) => {
    return new Date(timestamp).toLocaleTimeString([], { 
      hour: '2-digit', 
      minute: '2-digit' 
    })
  }

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-primary border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-muted-foreground">Loading chat...</p>
        </div>
      </div>
    )
  }

  if (!user) {
    return null // Will redirect to login
  }

  return (
    <div className="max-w-6xl mx-auto h-[calc(100vh-200px)] flex gap-4">
      {/* Members Sidebar */}
      <div className="w-64 bg-card border border-border rounded-lg p-4">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-lg font-semibold text-foreground">Members</h2>
          <button
            onClick={() => setShowMembers(!showMembers)}
            className="text-sm text-muted-foreground hover:text-foreground"
          >
            {showMembers ? 'Hide' : 'Show'}
          </button>
        </div>
        
        {showMembers && (
          <div className="space-y-2">
            {members.map((member) => (
              <div key={member.id} className="flex items-center justify-between p-2 rounded-md hover:bg-muted/50">
                <span className="text-sm font-medium text-foreground">{member.anonymousId}</span>
                <span className="text-lg">
                  {member.isOnline ? 'ðŸ¦œ' : 'âšª'}
                </span>
              </div>
            ))}
          </div>
        )}
        
        {!showMembers && (
          <p className="text-sm text-muted-foreground">
            {members.length} members online
          </p>
        )}
      </div>

      {/* Chat Area */}
      <div className="flex-1 flex flex-col">
        {/* Chat Header */}
        <div className="bg-card border border-border rounded-t-lg p-4 flex items-center justify-between">
          <div>
            <h1 className="text-xl font-bold text-foreground">College Group Chat</h1>
            <p className="text-sm text-muted-foreground">
              Chatting as: <span className="font-medium text-foreground">{user.anonymousId}</span>
            </p>
          </div>
          <button
            onClick={handleLogout}
            className="bg-destructive text-destructive-foreground px-4 py-2 rounded-md text-sm font-medium hover:bg-destructive/90 transition-colors"
          >
            Logout
          </button>
        </div>

        {/* Messages Area */}
        <div className="flex-1 bg-card border-l border-r border-border overflow-y-auto p-4 space-y-4">
          {messages.length === 0 ? (
            <div className="text-center text-muted-foreground py-8">
              <p>No messages yet. Start the conversation!</p>
            </div>
          ) : (
            messages.map((message) => (
              <div
                key={message.id}
                className={`flex ${
                  message.sender === user.anonymousId ? 'justify-end' : 'justify-start'
                }`}
              >
                <div
                  className={`max-w-xs lg:max-w-md px-4 py-2 rounded-lg ${
                    message.sender === user.anonymousId
                      ? 'bg-primary text-primary-foreground'
                      : 'bg-muted text-muted-foreground'
                  }`}
                >
                  <div className="flex items-center justify-between mb-1">
                    <span className="text-xs font-medium">
                      {message.sender === user.anonymousId ? 'You' : message.sender}
                    </span>
                    <span className="text-xs opacity-70">
                      {formatTime(message.timestamp)}
                    </span>
                  </div>
                  <p className="text-sm break-words">{message.text}</p>
                </div>
              </div>
            ))
          )}
          <div ref={messagesEndRef} />
        </div>

        {/* Message Input */}
        <div className="bg-card border border-border rounded-b-lg p-4">
          {error && (
            <div className="mb-4 p-3 bg-destructive/10 border border-destructive/20 rounded-md">
              <p className="text-sm text-destructive">{error}</p>
            </div>
          )}
          
          <form onSubmit={handleSendMessage} className="flex space-x-2">
            <input
              type="text"
              value={newMessage}
              onChange={(e) => setNewMessage(e.target.value)}
              placeholder="Type your message..."
              className="flex-1 px-3 py-2 border border-input rounded-md bg-background text-foreground focus:outline-none focus:ring-2 focus:ring-ring focus:border-transparent"
              disabled={isSending}
              maxLength={1000}
            />
            <button
              type="submit"
              disabled={isSending || !newMessage.trim()}
              className="bg-primary text-primary-foreground px-6 py-2 rounded-md font-medium hover:bg-primary/90 focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
            >
              {isSending ? 'Sending...' : 'Send'}
            </button>
          </form>
          
          <p className="text-xs text-muted-foreground mt-2">
            {newMessage.length}/1000 characters
          </p>
        </div>
      </div>
    </div>
  )
}
